*{
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
}

h1, h2, h3, h4, span, p, a, li, input {
    font-family: 'Poppins', sans-serif;
}

input {
    outline:none;
}

#container{
    max-width: 1400px;
    width: 90%;
    margin: 0 auto;
}

.header{
    padding: 25px 0;
    position: relative;
    z-index: 10;
    &-nav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        opacity: .8;
        &__item:first-child{
            a{
                color: #3670FF;
                font-size: 31px;
                font-weight: 600;
            }
        }
        &__item:last-child{
            display: flex;
            align-items: center;
        }
        &__element{
            a{
                font-weight: 500;
                color: #174AFF;
                border: 1px solid #174AFF;
                padding: 15px 30px;
                border-radius: 5px;
                margin-left: 50px;
            }
        }
    }
    &-nav2 &-nav__item:first-child a, &-nav2 &-list__item a{
        color: #fff;
    }
    &-nav2 &-nav__element a{
        color: #fff;
        border-color: #fff;
    }
    &-list{
        list-style: none;
        padding: 0;
        margin: 20px;
        display: flex;
        &__item{
            margin-right: 55px;
            a{
                color: #363636;
                text-decoration: none;
                font-size: 18px;
            }
            a.active{
                color: #174AFF;
                font-weight: 500;
            }
        }
    }
}

.offer{
    padding-top: 140px;
    &-background{
        img{
            position: absolute;
            left: 0;
            z-index: 5;
            top: 0;
            width: 100%;
            height: 900px;
        }
    }
    &-block{
        &__item:first-child{
            position: relative;
            z-index: 10;
        }
        &__item{
            img{
                position: absolute;
                z-index: 10;
                right: 0;
                top: 230px;
            }
        }
        &__header{
            h1{
                color: #fff;
                font-size: 54px;
                font-weight: 600;
                width: 670px;
                letter-spacing: 0.04em;
            }
            p{
                color: #fff;
                font-size: 20px;
                width: 720px;
                padding: 20px 0 50px;
            }
        }
        &__body{
            form{
                display: flex;
                align-items: center;
                input{
                    border-radius: 5px;
                    border: none;
                }
                input:first-child{
                    color: #363636;
                    padding: 17px 140px 17px 25px;
                    margin-right: 20px;
                    background: #fff;
                }
                input:last-child{
                    font-weight: bold;
                    color: #303030;
                    padding: 17px 33px;
                    background: #FFCF59;
                    cursor: pointer;
                }
            }
            p{
                font-size: 14px;
                color: #fff;
                opacity: .6;
                margin-top: 15px;
            }
        }
    }
}

.tools{
    padding-top: 330px;
    &-title{
        font-weight: 600;
        font-size: 34px;
        width: 520px;
        color: #363636;
    }
    &-text{
        opacity: .6;
        font-size: 18px;
        color: #363636;
        margin-top: 15px;
    }
    &-body{
        padding-top: 50px;
        display: flex;
        justify-content: space-between;
    }
    &-item{
        width: 25%;
        &__title{
            padding: 25px 0 5px;
            font-weight: bold;
            color: #363636;
            font-size: 20px;
        }
        &__subtitle{
            font-weight: 500;
            color: #363636;
        }
        &__text{
            color: #363636;
            margin-top: 10px;
            opacity: .7;
        }
    }
}

.practice{
    padding-top: 140px;
    &-title{
        font-weight: 600;
        font-size: 34px;
        color: #363636;
        width: 520px;
    }
    &-text{
        font-weight: 500;
        font-size: 20px;
        color: #363636;
        opacity: .6;
        margin-top: 15px;
    }
    &-body{
        padding-top: 50px;
        &__item{
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &__item:first-child, &__item:last-child{
            margin-left: 50px;
        }
        &__item:nth-child(2), &__item:last-child{
            margin-top: 80px;
        }
        &__item:nth-child(2){
            .practice-item:last-child{
                margin-left: 100px;
                .practice-item__title{
                    width: 350px;
                }
            }
            .practice-item:first-child{
                .practice-item__background{
                    left: 0;
                    top: 2100px;
                }
            }
        }
        &__item:last-child{
            .practice-item__background{
                top: 2500px;
            }
        }
    }
    &-item{
        &__subtitle{
            padding: 22px 0 10px;
            font-weight: 500;
            font-size: 18px;
            color: #363636;
            opacity: .6;
        }
        &__title{
            font-weight: 600;
            font-size: 22px;
            color: #363636;
            padding-bottom: 15px;
            width: 50%;
        }
        &__text{
            font-size: 16px;
            color: #363636;
            opacity: .6;
            width: 59%;
        }
        &__background{
            position: absolute;
            right: 0;
            top: 1350px;
            z-index: -5;
        }
    }
}